<template>
	<view style="width: 100%;height: 100%;justify-content: center;align-items: center;display: flex;"
		v-if="isShowLogin">
		<text
			style="width: 100px;height: 30px;background-color: #7900B2;color: #FFFFFF;text-align: center;font-size: 15px;line-height: 30px;border-radius: 15px;"
			@click="doLongin()">请登录</text>
	</view>
	<view class="content column" v-else>
		<!-- :style="'height:' + windowHeight + 'px;'" -->

		<view style="width: 100%;background-color: #FFFFFF;position: relative;"
			:style="'height:' + (statusBarHeight + 48) + 'px;'">
			<view class="row" style="width: 100%;height: 48px;position: absolute;bottom: 0;">
				<!-- #ifdef MP-WEIXIN -->
				<image src="../../static/search.png" mode="" class="returnImage" @click="doSearch()"></image>
				<text
					style="margin-left: calc(33.3% - 52px);height: 48px;font-size: 18px;line-height: 48px;color: #323232;font-weight: bold;width: 33.3%;text-align: center;">全部订单</text>
				<!-- #endif -->
				<!-- #ifdef APP-PLUS -->
				<text
					style="margin-left: 33.3%;height: 48px;font-size: 18px;line-height: 48px;color: #323232;font-weight: bold;width: 33.3%;text-align: center;">全部订单</text>
				<image src="../../static/search.png" mode="" class="weixin-rightBtn" @click="doSearch()"></image>
				<!-- #endif -->
			</view>

		</view>
		<scroll-view scroll-x="true" style="width: 100%;height: 40px;background-color: #FFFFFF;white-space: nowrap;"
			class="row">
			<view v-for="(item,index) in typeArray" :key="index"
				:class="typeIndex == index ? 'typeButton-odr-sel' : 'typeButton-odr'"
				@click="typeButtonClick(item,index)">
				{{item}}
			</view>
		</scroll-view>
		<swiper style="width: 100%;background-color: #F7F8F9;" :style="'height:' + scrollHeight + 'px;'"
			@animationfinish="animationfinish" :current="typeIndex">
			<swiper-item>
				<orderListV :height="scrollHeight" typeString="0" @cellItemClick="cellItemClick" @doPingjia="doPingjia">
				</orderListV>
			</swiper-item>
			<swiper-item>
				<orderListV :height="scrollHeight" typeString="1" @cellItemClick="cellItemClick" @doPingjia="doPingjia">
				</orderListV>
			</swiper-item>
			<swiper-item>
				<orderListV :height="scrollHeight" typeString="2" @cellItemClick="cellItemClick" @doPingjia="doPingjia">
				</orderListV>
			</swiper-item>
			<swiper-item>
				<orderListV :height="scrollHeight" typeString="3" @cellItemClick="cellItemClick" @doPingjia="doPingjia">
				</orderListV>
			</swiper-item>
			<swiper-item>
				<orderListV :height="scrollHeight" typeString="4" @cellItemClick="cellItemClick" @doPingjia="doPingjia">
				</orderListV>
			</swiper-item>
			<swiper-item>
				<orderListV :height="scrollHeight" typeString="5" @cellItemClick="cellItemClick" @doPingjia="doPingjia">
				</orderListV>
			</swiper-item>
		</swiper>
	</view>
</template>

<script>
	import orderListV from '../../components/orderlist/orderListView.vue'
	export default {
		components: {
			orderListV
		},
		data() {
			return {
				windowWidth: 0,
				windowHeight: 0,
				statusBarHeight: 0,
				scrollHeight: 0,
				menuButtonWidth: 0,
				typeArray: ['全部', '待支付', '待接单', '进行中', '已完成', '取消/退款'],
				typeIndex: 0,
				fromWhere: '',
				isShowLogin: 0
			}
		},
		onLoad() {
			this.statusBarHeight = getApp().globalData.top
			this.menuButtonWidth = getApp().globalData.menuButtonWidth
			console.log(this.statusBarHeight, this.menuButtonWidth)
			var res = uni.getSystemInfoSync()
			this.windowWidth = getApp().globalData.windowWidth
			this.windowHeight = res.windowHeight
			this.scrollHeight = this.windowHeight - this.statusBarHeight - 88
		},
		onShow() {
			var that = this
			uni.getStorage({
				key: 'mineChangeOrderType',
				success: res => {
					that.typeIndex = res.data
					console.log(res)
					uni.removeStorageSync('mineChangeOrderType');

				}
			})
			var value = uni.getStorageSync('userinfo');
			if (!value || !value.id || value.id == 0) {
				that.isShowLogin = 1
			}
		},
		methods: {
			doreturn() {
				if (this.fromWhere == 'abc') {
					uni.navigateBack({
						delta: 999
					})
				} else {
					uni.navigateBack({

					})
				}
			},
			typeButtonClick(item, index) {
				this.typeIndex = index
				// this.$refs.orderListV.changeindex(this.typeIndex)
			},
			animationfinish(e) {
				this.typeIndex = e.detail.current
				// this.$refs.orderListV.changeindex(this.typeIndex)
			},
			doSearch() {
				uni.navigateTo({
					url: './orderSearchVC'
				})
			},
			cellItemClick(item, index) {
				if (item.status == 3 || item.status == 4) {
					uni.navigateTo({
						url: './order-map-detaile?order=' + item.id
					})
				} else {
					uni.navigateTo({
						url: './orderdetail?order=' + item.id
					})
				}
			},
			doPingjia(item) {
				uni.navigateTo({
					url: './orderEvaluate?orderID=' + item.id
				})
			},
			doLongin() {
				uni.navigateTo({
					url: '../login/login'
				})
			}

		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		background-color: #F7F8F9;
		overflow: hidden;
	}

	.content {
		width: 100%;
		height: 100%;
	}

	.returnImage {
		margin-left: 20px;
		margin-top: 13px;
		height: 20px;
		width: 20px;
	}

	.weixin-rightBtn {
		margin-top: 13px;
		height: 20px;
		width: 20px;
		right: 12px;
		position: absolute;
	}

	.typeButton-odr {
		display: inline-block;
		width: 70px;
		height: 40px;
		line-height: 40px;
		text-align: center;
		color: #646464;
		font-size: 14px;
	}

	.typeButton-odr-sel {
		display: inline-block;
		width: 70px;
		height: 40px;
		line-height: 40px;
		text-align: center;
		color: #323232;
		font-size: 14px;
		font-weight: bolder;
		position: relative;
	}

	.typeButton-odr-sel::after {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		bottom: 2px;
		width: 28px;
		height: 4px;
		margin: 0 auto;
		background-color: #FC6524;
		border-radius: 2px;
	}
</style>
